import ActivityProperty from "./components/property/index";
import InfoParagraph from "./components/paragraph/index";
import UserInfo from "./components/user-info/index";
export default {
  components: {
    ActivityProperty: ActivityProperty,
    InfoParagraph: InfoParagraph,
    UserInfo: UserInfo
  },
  data: function data() {
    return {
      activityId: null,
      data: null,
      loading: true,
      tabList: [{
        key: 'content',
        label: '活动详情',
        scopedSlots: {
          tab: 'content'
        }
      }, {
        key: 'property',
        label: '活动属性',
        scopedSlots: {
          tab: 'property'
        }
      }, {
        key: 'user',
        label: '申请用户',
        scopedSlots: {
          tab: 'applyUser'
        }
      }],
      key: 'content',
      headStyle: {
        'font-size': '22px',
        'color': '#000'
      }
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      this.activityId = this.$route.params.id;
      this.getInfo(this.activityId);
    },
    getInfo: function getInfo(activityId) {
      var _this = this;

      apiService.showActivity(activityId).then(function (res) {
        _this.data = res.data;
        _this.loading = false;
      });
    },
    onTabChange: function onTabChange(key, type) {
      console.log(key, type);
      this[type] = key;
    }
  }
};